import Vue from 'vue'
import Router from 'vue-router'
import mainpage from '@/components/mainpage'
import login from '@/components/login'
import loginmobile from '@/components/login-mobile'
import mainmobile from '@/components/main-mobile'
import chatmobile from '@/components/chat-mobile'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/mainpage',
      name: 'mainpage',
      component: mainpage
    },
    {
    	path:'/login',
    	name:'login',
    	component:login
    },
    {
      path:'/login-mobile',
      name:'login-mobile',
      component:loginmobile
    },
     {
      path:'/main-mobile',
      name:'main-mobile',
      component:mainmobile
    },
     {
      path:'/chat-mobile',
      name:'chat-mobile',
      component:chatmobile
    }

  ]
})



