<template>
	<div id="main">
		<div id="head">
			<n3-icon id="icon" type="github" size="200px"></n3-icon>
			<br>
			<span id="qq">&nbsp;Go Chat</span>
		</div>
		<div id="box">
			<div id="logtext">
				<input v-model="username" type="text" class="textbox" placeholder="请输入账号">
				<br>
				<input v-model="userpassword" type="password" class="textbox" placeholder="请输入密码">
				<br>
				<div @click="loginbtn">
					<n3-button type="info" class="enter">登 &nbsp;录</n3-button>
				</div>
				<br>
				<div class="link">
				<a id="link1" class="link">注册账号</a>
				<a id="link2" class="link">找回密码</a>
				</div>
			</div>
		</div>
	</div>
</template>

<script >
	export default{
		name: 'login-mobile',
		data(){
			return{
				username:'',
				userpassword:'',
			}
			
		},
		methods:{
			loginbtn: function (){
				if (this.username!=''&&this.userpassword!='')
				{
					this.$router.push('main-mobile')
				}
			}
		}
	}

  
</script>

<style scoped>

@keyframes myfirst
{
0%   {background-color:#55DDFF ;}
25%  {background-color:#33CCFF ;}
50%  {background-color:#1188FF ;}
75%  {background-color:#33CCFF ;}
100% {background-color:#55DDFF ;}
}

@-webkit-keyframes myfirst 
{
0%   {background-color:#55DDFF ;}
25%  {background-color:#33CCFF ;}
50%  {background-color:#1188FF ;}
75%  {background-color:#33CCFF ;}
100% {background-color:#55DDFF ;}
}

@-moz-keyframes myfirst
{
0%   {background-color:#55DDFF ;}
25%  {background-color:#33CCFF ;}
50%  {background-color:#1188FF ;}
75%  {background-color:#33CCFF ;}
100% {background-color:#55DDFF ;}
}

@-o-keyframes myfirst
{
0%   {background-color:#55DDFF ;}
25%  {background-color:#33CCFF ;}
50%  {background-color:#1188FF ;}
75%  {background-color:#33CCFF ;}
100% {background-color:#55DDFF ;}
}


	#main{
		background-color: white;
		height: 1300px;
		width: 100%
	}
	#head{
		-webkit-animation: myfirst 7s linear 1s infinite alternate;
    	-moz-animation: myfirst 7s linear 1s infinite alternate;
        -o-animation: myfirst 7s linear 1s infinite alternate;
        animation: myfirst 7s linear 1s infinite alternate;
		height: 560px;
	}
	#icon{
		margin: 200px 0 0 42% ;
		color: white;
	}
	#qq{
		margin: 200px 0 0 35% ;
		color: white;
		font-size: 70px;
	}
	#box{
		height: 1000px;
		background-color: white;
		padding: 100px 0 0 0;
	}
	.textbox{
		margin: 0 0 0 17.5%;
		height: 80px;
		width: 65%;
		border:solid 1px;
		border-color: gray;
		border-radius: 15px;
		padding: 25px;
		font-size: 32px;
	}
	.enter{
		margin: 100px 0 0 17.5%;
		height: 80px;
		width: 65%;
		border-radius: 15px;
		padding: 15px 0 0 0;
		font-size: 36px;
	}
	.link{
		margin: 200px 0 0 17%;
		font-size: 28px;
		color: #3388DD;
		background-color: white;
	}
</style>


