<template>
<div>
	<div class="loginbg">
		<n3-icon id="icon" type="github" size="70px"></n3-icon>
		<span id="qq">&nbsp;Go Chat</span>
	</div>
	<div class="loginmain">
		<div id="headicon">
		</div> 
		<div id="logtext">
			<input v-model="username" type="text" class="textbox" placeholder="请输入账号">
			<input v-model="userpassword" type="password" class="textbox" placeholder="请输入密码">
			<div @click="loginbtn">
				<n3-button type="info" class="enter" >登 &nbsp;录</n3-button>
			</div>	
		</div>
		<div>
			<div class="linkA" >
				<a id="link1">注册账号</a>
			</div>
			<div class="linkB" >
				<a id="link2">找回密码</a>
			</div>
		</div>
	</div>

</div>
</template>

<script >
	export default{
		name: 'login',
		data(){
			return{
				username:'',
				userpassword:'',
			}
			
		},
		methods:{
			loginbtn: function (){
				if (this.username!=''&&this.userpassword!='')
				{
					this.$router.push('mainpage')
				}
			}
		}
	}

</script>

<style scoped>


@keyframes myfirst
{
0%   {background-color:#55DDFF ;}
25%  {background-color:#33CCFF ;}
50%  {background-color:#1188FF ;}
75%  {background-color:#33CCFF ;}
100% {background-color:#55DDFF ;}
}

	.loginbg{
		animation: myfirst 7s linear 1s infinite alternate;
		margin:240px auto auto auto;
		text-align: center;
		height: 180px;
		width: 435px;
		border:solid 1px;
		border-color: gray;
		background-color:#33CCFF;

	}

	#qq{
		color: white;
		font-size: 45px;
		font-family:"Segoe UI", Frutiger, "Frutiger Linotype", "Dejavu Sans", "Helvetica Neue", Arial,sans-serif;
		line-height: 210px;
	}

	#icon{
		color: white;
	}

	.loginmain{
		margin: auto;
		text-align: center;
		height: 150px;
		width: 435px;
		border:solid 1px;
		border-color:gray; 
		background-color:#F1F9FF;
	}
	#logtext{
		float: left;
		margin: 6% 1% 5% 0;
		height: 100px;
		width: 45%;
		background-color:#F1F9FF;
	}
	#headicon{
		margin: 3% 3% 3% 8%;
		float: left;
		height: 90px;
		width: 90px;
		border-radius: 120px;
		background-size: 90px;
		background-image: url(https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1493710800358&di=674e5d60fd2a78d0ab5e9f81be71b78d&imgtype=0&src=http%3A%2F%2Fcfanz.cn%2Fuploads%2Fpng%2F2014%2F06%2F10%2F10%2FYL41W69H3C.png);
	}

	.textbox{
		border-radius: 5px;
		height: 30px;
		width: 190px;
		border:solid 1px;
		float: left;
		padding: 10px;
		border-color: lightgray;
	}
	.linkA
	{
		margin: 7% 0 0 0;
		float: left;
		height: 30px;
		width: 60px;
	}
	.linkB
	{
		margin: 0 0 0 0;
		float: left;
		height: 30px;
		width: 60px;
	}
	#link1{
		font-size: 12px;
		color: #3388DD;
	}
	#link2{
		font-size: 12px;
		color: #3388DD;
	}
	.enter{
		margin: 10% 0 0 -1%;
		width: 190px;
		height: 30px;
		background-color: #22AAE7;
		border-width: 0;
		border-radius: 5px;
		font-size: 5px;
		color:white;
	}



</style>


